const sizePerPageRenderer = ({
  options,
  currSizePerPage,
  onSizePerPageChange,
}) => {
  return (
    <UncontrolledDropdown className="d-inline-block m-l-5">
      <DropdownToggle caret color="light" className="border">
        {currSizePerPage}
      </DropdownToggle>
      <DropdownMenu>
        {options.map(option => (
          <DropdownItem
            key={option.text}
            onClick={() => onSizePerPageChange(option.page)}
          >
            {option.text}
          </DropdownItem>
        ))}
      </DropdownMenu>
    </UncontrolledDropdown>
  )
}
const customTotal = (from, to, size) => (
  <span className="react-bootstrap-table-pagination-total">
    Showing {from} to {to} of {size} Results
  </span>
)

const options = {
  custom: true,
  totalSize: products.length,
  sizePerPageRenderer,
  showTotal: true,
  paginationTotalRenderer: customTotal,
  prePageText: "Previous",
  nextPageText: "Next",
  withFirstAndLast: false,
}

class DataTable extends Component {
  render() {
    const contents = ({ paginationProps, paginationTableProps }) => (
      <Fragment>
        <ToolkitProvider keyField="id" columns={columns} data={products} search>
          {toolkitprops => (
            <Fragment>
              <Row>
                <Col sm={6}>
                  <label>
                    Show
                    <SizePerPageDropdownStandalone {...paginationProps} />{" "}
                    entries
                  </label>
                </Col>
                <Col sm={6} className="text-right">
                  <span>
                    <span className="p-r-5"> Search:</span>

                    <SearchBar {...toolkitprops.searchProps} />
                  </span>
                </Col>
              </Row>
              <BootstrapTable
                keyField="id"
                HeaderCell=""
                data={products}
                columns={columns}
                wrapperClasses="table-responsive"
                striped
                hover
                condensed
                {...toolkitprops.baseProps}
                {...paginationTableProps}
              />
            </Fragment>
          )}
        </ToolkitProvider>
        <PaginationTotalStandalone {...paginationProps} />
        <PaginationListStandalone {...paginationProps} />
      </Fragment>
    )

    return (
      <Row>
        <Col sm={12}>
          <Card>
            <CardHeader>
              <h5>Zero Configuration</h5>
            </CardHeader>
            <CardBody>
              <PaginationProvider pagination={paginationFactory(options)}>
                {contents}
              </PaginationProvider>
            </CardBody>
          </Card>
        </Col>
        <Col sm={12}>
          <Card>
            <CardHeader>
              <h5>Responsive Table</h5>
            </CardHeader>
            <CardBody>
              <PaginationProvider pagination={paginationFactory(options)}>
                {contents}
              </PaginationProvider>
            </CardBody>
          </Card>
        </Col>
        <Col sm={12}>
          <Card>
            <CardHeader>
              <h5>Scrolling Table</h5>
            </CardHeader>
            <CardBody>
              <ToolkitProvider
                keyField="id"
                columns={columns}
                data={products}
                search
              >
                {toolkitprops => (
                  <Fragment>
                    <Row>
                      <Col sm={12} className="text-right m-b-10">
                        <span>
                          <span className="p-r-5"> Search:</span>

                          <SearchBar {...toolkitprops.searchProps} />
                        </span>
                      </Col>
                    </Row>
                    <BootstrapTable
                      keyField="id"
                      HeaderCell=""
                      data={products}
                      columns={columns}
                      wrapperClasses="table-responsive scrollable-able"
                      bordered={false}
                      striped
                      hover
                      condensed
                      {...toolkitprops.baseProps}
                    />
                  </Fragment>
                )}
              </ToolkitProvider>
            </CardBody>
          </Card>
        </Col>
      </Row>
    )
  }
}

export default DataTable
